<template>
  <div style="display: flex; justify-content: center;">
    <div id="watchRequest">
      <div>
        <h1>
          Watch命令请求参数
        </h1>
      </div>

      <div>
        <label>classPattern: </label>
        <input type="text" v-model="watchRequestModel.classPattern">
      </div>

      <div>
        <label>methodPattern: </label>
        <input type="text" v-model="watchRequestModel.methodPattern">
      </div>

      <div>
        <label>express: </label>
        <input type="text" v-model="watchRequestModel.express">
      </div>

      <div>
        <label>conditionExpress: </label>
        <input type="text" v-model="watchRequestModel.conditionExpress">
      </div>

      <div>
        <label>isBefore: </label>
        <input type="checkbox" v-model="watchRequestModel.isBefore">
      </div>

      <div>
        <label>isFinish: </label>
        <input type="checkbox" v-model="watchRequestModel.isFinish">
      </div>

      <div>
        <label>isException: </label>
        <input type="checkbox" v-model="watchRequestModel.isException">
      </div>

      <div>
        <label>isSuccess: </label>
        <input type="checkbox" v-model="watchRequestModel.isSuccess">
      </div>

      <div>
        <label>expand: </label>
        <input type="number" v-model="watchRequestModel.expand">
      </div>

      <div>
        <label>sizeLimit: </label>
        <input type="number" v-model="watchRequestModel.sizeLimit">
      </div>

      <div>
        <label>isRegEx: </label>
        <input type="checkbox" v-model="watchRequestModel.isRegEx">
      </div>

      <div>
        <label>numberOfLimit: </label>
        <input type="number" v-model="watchRequestModel.numberOfLimit">
      </div>

      <div>
        <label>excludeClassPattern: </label>
        <input type="text" v-model="watchRequestModel.excludeClassPattern">
      </div>

      <div>
        <label>listenerId: </label>
        <input type="number" v-model="watchRequestModel.listenerId">
      </div>

      <div>
        <label>verbose: </label>
        <input type="checkbox" v-model="watchRequestModel.verbose">
      </div>

      <div>
        <label>maxNumOfMatchedClass: </label>
        <input type="number" v-model="watchRequestModel.maxNumOfMatchedClass">
      </div>

      <div>
        <label>jobId: </label>
        <input type="number" v-model="watchRequestModel.jobId">
      </div>

      <div style="display: flex; justify-content: space-between;">
        <button type="primary"  style="margin-right: 10px;" @click="sendWatchRequest">发送请求</button>
        <button type="primary"  style="margin-right: 10px;" @click="stopWatchRequest">取消请求</button>
        <button type="primary" @click="clear">清除结果</button>
      </div>
    </div>


    <div id="objectRequest">
      <div>
        <h1>
          获取历史数据请求参数
        </h1>
        <p>请先使用watch命令获取数据,</p>
        <p>点击表格最后一栏的按钮获取想要查询的信息后</p>
        <p>再点击查询</p>
      </div>
      <div>
        <label>jobId: </label>
        <input type="number" v-model="objectRequestModel.jobId">
      </div>

      <div>
        <label>resultId: </label>
        <input type="number" v-model="objectRequestModel.resultId">
      </div>

      <div>
        <label>type: </label>
        <input type="text" v-model="objectRequestModel.type">
      </div>

      <div>
        <label>express: </label>
        <input type="text" v-model="objectRequestModel.express">
      </div>

      <div>
        <label>expand: </label>
        <input type="number" v-model="objectRequestModel.expand">
      </div>

      <div style="display: flex; justify-content: space-between;">
        <button type="primary"  style="margin-right: 10px;" @click="sendObjectRequest">查询</button>
      </div>
    </div>

    <div id="objectDisplay">
      <h1>历史数据展示区域</h1>
      <h3 class="preserve-whitespace2">{{objectDisplay}}</h3>
    </div>

  </div>

  <div>
    <h1>{{pwdResponse}}</h1>
  </div>
  <button type="primary" @click="sendPwdRequest">pwd</button><br>


  <table>
    <thead>
    <tr>
      <th>jobid</th>
      <th>resultid</th>
      <th>ts</th>
      <th>accessPoint</th>
      <th>className</th>
      <th>methodName</th>
      <th>cost</th>
      <th>value</th>
      <th>获取当前结果信息</th> <!-- 添加操作栏的表头 -->
    </tr>
    </thead>
    <tbody>
    <tr v-for="(item, index) in tableData" :key="index">
      <td>{{ item.jobId }}</td>
      <td>{{ item.resultId }}</td>
      <td>{{ item.ts }}</td>
      <td>{{ item.accessPoint }}</td>
      <td>{{ item.className }}</td>
      <td>{{ item.methodName }}</td>
      <td>{{ item.cost }}</td>
      <td class="preserve-whitespace">{{ item.value }}</td>
      <td>
        <button @click="handleClick(item)">获取信息</button> <!-- 添加按钮，并绑定点击事件 -->
      </td>
    </tr>
    </tbody>
  </table>


</template>

<script>
// 引入自动生成的grpc_web相关的文件

import {
  WatchClient,
  PwdClient,
  ObjectServiceClient
} from '@/assets/proto/ArthasService_grpc_web_pb';

import { WatchRequest, ObjectRequest } from '@/assets/proto/ArthasService_grpc_web_pb';
// import {
//   EchoServiceClient
// } from '@/assets/proto/echo_grpc_web_pb';
// import { EchoRequest } from '@/assets/proto/echo_pb';
import { Empty } from 'google-protobuf/google/protobuf/empty_pb';


export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'HelloWorld',
  data(){
    return{
      watchClient : null,
      pwdClient: null,
      objectClient: null,
      metadata : {},
      stream : null,
      watchRequestModel : {
        classPattern : "demo.MathGame",
        methodPattern : "primeFactors",
        express : "",
        conditionExpress : "",
        isBefore : false,
        isFinish : true,
        isException : false,
        isSuccess : false,
        expand : 2,
        sizeLimit : 10 * 1024 * 1024,
        isRegEx : false,
        numberOfLimit : 10,
        excludeClassPattern : "",
        listenerId : 0,
        verbose : false,
        maxNumOfMatchedClass : 50,
        jobId : 0
      },
      objectRequestModel : {
        jobId: 0,
        resultId: 0,
        type:"",
        express: "{params, target, returnObj}",
        expand: 3
      },
      tableData: [], // 存储表格数据的数组
      pwdResponse: "www",
      objectDisplay: "objectDisplay"
    }
  },

  created() {
    this.watchClient = new WatchClient('http://localhost:8567');
    this.pwdClient = new PwdClient('http://localhost:8567');
    this.objectClient = new ObjectServiceClient('http://localhost:8567')
    // this.echoclient = new EchoServiceClient('http://localhost:8567');
    // this.sendTest();
    this.metadata = {"Content-Type": "application/grpc-web-text"};
  },

  methods:{
    sendPwdRequest(){
      var pwdRequest  = new Empty();
      this.pwdClient.pwd(pwdRequest, {}, (error, response) => {
        if (!error) {
          // 处理成功响应
          const jobId = response.getJobid();
          const type = response.getType();
          if(type == "pwd" && response.hasStringstringmapvalue()){
            var stringstringmapvalue = response.getStringstringmapvalue();
            var result = stringstringmapvalue.getStringstringmapMap().get("workingDir")
            this.pwdResponse = "jobid:" + jobId + "\n" + result
          }
        } else {
          // 处理错误
          console.error(error);
        }
      });
    },

    sendWatchRequest(){
      const watchRequest = new WatchRequest();
      watchRequest.setClasspattern(this.watchRequestModel.classPattern)
          .setMethodpattern(this.watchRequestModel.methodPattern)
          .setExpress(this.watchRequestModel.express)
          .setConditionexpress(this.watchRequestModel.conditionExpress)
          .setIsbefore(this.watchRequestModel.isBefore)
          .setIsfinish(this.watchRequestModel.isFinish)
          .setIsexception(this.watchRequestModel.isException)
          .setIssuccess(this.watchRequestModel.isSuccess)
          .setExpand(this.watchRequestModel.expand)
          .setSizelimit(this.watchRequestModel.sizeLimit)
          .setIsregex(this.watchRequestModel.isRegEx)
          .setNumberoflimit(this.watchRequestModel.numberOfLimit)
          .setExcludeclasspattern(this.watchRequestModel.excludeClassPattern)
          .setListenerid(this.watchRequestModel.listenerId)
          .setVerbose(this.watchRequestModel.verbose)
          .setMaxnumofmatchedclass(this.watchRequestModel.maxNumOfMatchedClass)
          .setJobid(this.watchRequestModel.jobId);

      let stream;
      stream = this.watchClient.watch(watchRequest,{});
      let _this = this
      // 持续获取流数据并处理
      stream.on('data', function(response) {
        const jobId = response.getJobid();
        const type = response.getType();
        const resultId = response.getResultid();
        if(type == "watch" && response.hasWatchresponse()){
          const watchResponse = response.getWatchresponse();
          const newData = {
            jobId: jobId,
            resultId: resultId,
            type: type,
            ts: watchResponse.getTs(),
            accessPoint: watchResponse.getAccesspoint(),
            className: watchResponse.getClassname(),
            methodName: watchResponse.getMethodname(),
            cost: watchResponse.getCost(),
            value: watchResponse.getValue()
          };
          console.log(watchResponse.getValue())
          _this.tableData.unshift(newData);
          _this.watchRequestModel.jobId = jobId;
          _this.stream = stream;
        }else {
          console.log("收到的不是watchResponse: ----->")
          console.log('type:', type);
          console.log('message:', response.getStringvalue());
        }
      });

      stream.on('status', function(status) {
        console.log("status.code " + status.code);
        console.log("status.details " + status.details);
        console.log("status.metadata " + status.metadata.toString());
      });

      stream.on('end', function(end) {
        console.log("end: " + end)
        // stream end signal
        stream.cancel()
      });

    },

    stopWatchRequest(){
      this.stream.cancel();
    },

    clear(){
      this.tableData = []
    },
    handleClick(item) {
      // 在这里处理按钮点击事件，可以使用item对象获取当前行的信息
      this.objectRequestModel.jobId = item.jobId;
      this.objectRequestModel.resultId = item.resultId;
      this.objectRequestModel.type = item.type;
    },

    sendObjectRequest(){
      const objectRequest = new ObjectRequest();
      objectRequest.setJobid(this.objectRequestModel.jobId)
          .setResultid(this.objectRequestModel.resultId)
          .setType(this.objectRequestModel.type)
          .setExpress(this.objectRequestModel.express)
          .setExpand(this.objectRequestModel.expand)

      this.objectClient.getInstance(objectRequest, {}, (error, response) => {
        if (!error) {
          // 处理成功响应
          const jobId = response.getJobid();
          const type = response.getType();
          const resultId = response.getResultid();
          if(response.hasStringvalue()){
            var stringvalue = response.getStringvalue();
            this.objectDisplay ="type:" + type + " jobid: " + jobId + " resultId:"  + resultId + "\n" + stringvalue
          }
        } else {
          // 处理错误
          console.error(error);
        }
      });

    }
  }
}

</script>



<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

#watchRequest {
   display: flex;
   flex-direction: column;
   align-items: flex-start; /* 左对齐 */
   justify-content: center
 }
#objectRequest {
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* 左对齐 */
  justify-content: center
}
label {
  margin-right: 10px; /* 标签与输入框之间的右边距 */
  align-items: flex-start; /* 左对齐 */
}

table {
  border-collapse: collapse;
  width: 100%;
}

th, td {
  border: 1px solid #ccc;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
}
.preserve-whitespace {
  white-space: pre-wrap; /* 保留换行符并折叠连续的空白字符 */
}

.preserve-whitespace2 {
  white-space: pre-wrap; /* 保留换行符并折叠连续的空白字符 */
  text-align: left;

}
</style>
